@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
html {
  height: 100%;
}
body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.main-container {
  width: 100%;
  flex-grow: 1;
}
a {
  text-decoration: none;
}
.reset-list {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
}
.page-wrapper {
  position: relative;
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page-wrapper {
    width: 709px;
  }
}
@media (min-width: 1440px) {
  .page-wrapper {
    width: 1220px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  display: block;
  margin: 0;
  min-height: 65px;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  word-wrap: wrap;
  color: #000000;
}
.button-catalog {
  display: block;
  margin: 0;
  padding: 10px;
  width: 100%;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  color: #ffffff;
  background-color: #68b738;
  border: none;
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    justify-content: space-between;
    width: 709px;
    margin: 0 auto;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    width: 1220px;
  }
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-wrap: nowrap;
  }
}
.main-nav__logo {
  display: flex;
  padding-top: 10px;
  padding-bottom: 16px;
}
@media (min-width: 768px) {
  .main-nav__logo {
    width: 200px;
    padding-top: 23px;
    margin: 0;
    margin-right: 20px;
  }
}
@media (min-width: 1440px) {
  .main-nav__logo {
    padding-top: 52px;
    width: 240px;
  }
}
.main-nav__logo-link {
  width: 68%;
  display: flex;
  justify-content: space-between;
}
