.main-nav--closed .main-nav__inner::before {
  position: absolute;
  top: -33px;
  right: 0;
  display: block;
  content: "";
  width: 25px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 -8px 0 0 #000000, 0 8px 0 0 #000000;
}
/*крестик в меню*/
.main-nav--opened .main-nav__inner::before,
.main-nav--opened .main-nav__inner::after {
  display: block;
  content: "";
  position: absolute;
  top: -35px;
  right: 0;
  width: 26px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__inner::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__inner::after {
  transform: rotate(-45deg);
}
